@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}
body {
  background: #1C8EF9 !important;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}
.span3 {
  margin: 20px;
}
.span1 {
  margin: 20px;
}

.aboutus-section {
  padding: 90px 0;
}
.aboutus-title {
  font-size: 30px;
  letter-spacing: 0;
  line-height: 32px;
  margin: 0 0 39px;
  padding: 0 0 11px;
  position: relative;
  text-transform: uppercase;
  color: #000;
}
.aboutus-title::after {
  background: #fdb801 none repeat scroll 0 0;
  bottom: 0;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  width: 54px;
}
.aboutus-text {
  color: #606060;
  font-size: 13px;
  line-height: 22px;
  margin: 0 0 35px;
}
.aboutus-more {
  border: 1px solid #fdb801;
  border-radius: 25px;
  color: #fdb801;
  display: inline-block;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0;
  padding: 7px 20px;
  text-transform: uppercase;
}

#error {
  font-size: smaller;
  color: red;
}

#img {
  border-style: ridge;
  border-radius: 10px;
  box-shadow: 10px 10px 20px -15px rgba(0,0,0,0.75);
}

img {
  border-radius: 10px;
}

#bg-body {
  transition: all 0.8s ease-in;
}

/* Calculator */

/* Grid */
.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.calc-container {
  display: grid;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.dot {
  grid-column: span 2;
}

.equal {
  grid-row: span 2;
}

#buttons-container {
  display: grid;
  grid-template-columns: repeat(4, auto);
  text-align: center;
  
}

/* Input */
#input {
  width: 100%;
  padding-right: 10px;
  font-size: 3em;
  text-align: right;
  height: 2em;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #FDFEFE;
  background-color: #223850;
  transition: all 0.3s ease-in
}

/* Btn's */
.btn_calc {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  font-size: 1em;
  cursor: pointer;
  transition: all 0.3s ease-in;
  
}

.btn-num {
  background-color: #223850;
  color: #6885A7;
}

.btn-num:hover {
  background-color: #233B55;
}

.btn-operator {
  font-weight: bold;
  background-color: #233B55;
  color: #FDFEFE;
}

.btn-operator:hover {
  background-color: rgb(27, 47, 68) !important
}

#color-change {
  position: fixed;
  border-radius: 50%;
  bottom: 0;
  right: 0;
  font-size: 1em;
  margin: 1em;
  transition: all 0.3s ease-in;
  background-color: #223850;
  color: #FDFEFE;
}

#color-change:hover {
  background-color: #FDFEFE !important;
  color: #233B55 !important;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

/* Color Modes */
.white-bg {
  background-color: #FDFEFE !important;
}
.white-text {
  color: #FDFEFE !important;
}
.blue-bg {
  background-color: #223850 !important;
}
.blue-text {
  color: #223850 !important;
}
